.order-form {
    background: linear-gradient(277deg, rgba(239, 60, 21, 0.65) 1.94%, rgba(245, 138, 115, 0.33) 20.49%, rgba(252, 214, 205, 0.84) 76.29%, rgba(239, 60, 21, 0.56) 100%);

    &__wrapper {
        padding: 76px 0 83px 0;
    }

    .form {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 741px;
        margin: 0 auto;
        padding: 40px 100px;
        background-color: $white;
        border-radius: 32px;

        &::before {
            position: absolute;
            content: "";
            background-image: url("../img/main/form/pack.png");
            background-size: contain;
            background-repeat: no-repeat;
            width: 418px;
            height: 531px;
            top: -10px;
            left: -403px;
        }

        &::after {
            position: absolute;
            content: "";
            background-image: url("../img/main/form/coffee-red.png");
            background-size: contain;
            background-repeat: no-repeat;
            width: 311px;
            height: 459px;
            top: -20px;
            right: -340px;
            z-index: 2;
        }

        &__image {
            position: relative;

            &::after {
                position: absolute;
                content: "";
                background-image: url("../img/main/form/coffee-gray.png");
                background-size: contain;
                background-repeat: no-repeat;
                width: 512px;
                height: 700px;
                top: -293px;
                right: -1211px;
                z-index: 1;
            }
        }

        &__title {
            font-size: 40px;
            font-style: normal;
            font-weight: 900;
            line-height: 60px;
            margin-bottom: 18px;
            text-align: center;
        }

        &__description {
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 27px;
            margin-bottom: 23px;

            span {
                color: rgba(239, 60, 21, 0.82);
            }
        }

        &__user-name {
            width: 100%;
            font-size: 22px;
            font-weight: 400;
            line-height: 40px;
            border: none;
            border-bottom: 1px solid #333;
            color: var(#828282);
            margin-bottom: 22px;
        }

        &__user-phone {
            width: 100%;
            font-size: 22px;
            font-weight: 400;
            line-height: 33px;
            border: none;
            border-bottom: 1px solid #333;
            color: var(#828282);
            margin-bottom: 24px;
        }

        &__checkbox-wrapper {
            display: flex;
            flex-direction: row;
            width: 100%;
            margin-bottom: 42px;

            input {
                margin-right: 6px;
                outline: 0;
                &:active {
                    outline-offset: 0;
                }
            }
        }

        &__privacy {
            color: #333;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;
        }

        &__privacy-link {
            color: var(--cp-182, rgba(239, 60, 21, 0.82));
            text-decoration: underline;
        }

        &__button {
            display: block;
            width: 100%;
            color: $white;
            font-size: 26px;
            font-style: normal;
            font-weight: 700;
            padding: 16px 0;
            border: none;
            border-radius: 32px;
            background: #EF3C15;
        }
    }
}